import React from 'react';
import classNames from 'classnames'
import './CategoryButton.css'

const CategoryButton = ({title, className, color, calls}) => {
	const classes = classNames(
		'mt-4 mx-lg-3 mx-0 d-flex align-items-center p-3 categoryBtn',
		color,
		className,
	);
	return (
		<div className={classes}>
			<p className="text-white sf-bold mb-0">
				{title}
			</p>
			{calls &&
			<span className="text-white sf-bold mb-0 bg-danger px-3 py-3 mx-0 categoryBtn__action">
				Х
			</span>
			}
		</div>
	);
};

export default CategoryButton;