import React, {Component} from 'react';
import Icon from "../Icon/Icon";
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';

import '../addNewClient/AddNewClient.css';


class AddClient extends Component {
	state = {
		selectedDate: new Date(),
		description: '',
		potential: false,
		name: '',
		number: '',
		company: '',
		social: '',
	};
	handleChange = field => e => {
		this.setState({
			[field]: e.target.value
		})
	};
	setPotential = () => {
		this.setState({potential: !this.state.potential})
	}
	render() {
		return (
			<div className='addNewClient'>
				<Icon name="users fa-4x" size={4} className="mb-3 animated addNewClient__icon rotateIn"/>
				<TextField
					label="ФИО"
					className='addNewClient__textField'
					margin="normal"
					value={this.state.name}
					onChange={this.handleChange('name')}
				/>
				<TextField
					label="Номер телефона"
					className='addNewClient__textField'
					margin="normal"
					value={this.state.number}
					onChange={this.handleChange('number')}
				/>
				<TextField
					label="Компания"
					className='addNewClient__textField'
					margin="normal"
					value={this.state.company}
					onChange={this.handleChange('company')}
				/>
				<TextField
					label="Сайт или соц. сети"
					className='addNewClient__textField'
					margin="normal"
					value={this.state.social}
					onChange={this.handleChange('social')}
				/>
				<TextField
					label="Описание"
					multiline
					value={this.state.description}
					onChange={this.handleChange('description')}
					rows={4}
					className='addNewClient__textField'
					margin="normal"
				/>
				<FormControlLabel
					control={
						<Checkbox
							checked={this.state.potential}
							onChange={this.setPotential}
							color="primary"
						/>
					}
					label="Потенциальный клиент?"
				/>
			</div>
		);
	}
}

export default AddClient;