import { EDIT_LOGIN, LOGIN_SUCCESS, SAVE_TOKEN, SAVE_USER} from "../../constants";

const INITIAL_STATE = {
	login: {
		"email": "",
		"password": ''
	},
	logged: false,
	token: '',
	user: {},
};

export function initialSettings(state = INITIAL_STATE, {type, key, value, token, user}) {
	switch (type) {
		case EDIT_LOGIN:
			return {...state, login: {...state.login, [key]: value}};
		case SAVE_TOKEN:
			return {...state, token: token};
		case LOGIN_SUCCESS:
			localStorage.setItem('token', token);
			return {...state, logged: true};
		case SAVE_USER:
			return {...state, user: user};
		default:
			return state

	}
}