import React from 'react';
import './mainSidebar.css';
import {Link} from "react-router-dom";
import home from '../../assets/images/home.svg';
import tasks from '../../assets/images/edit-task.svg';
import calls from '../../assets/images/call.svg';
import meets from '../../assets/images/partnership.svg';
import clients from '../../assets/images/customer.svg';
import stats from '../../assets/images/graph.svg';

const links = [
	{id: 'link_1', href: '/home', title: 'Главная', icon: home},
	{id: 'link_2', href: '/tasks', title: 'Задачи', icon: tasks},
	{id: 'link_3', href: '/calls', title: 'Звонки', icon: calls},
	{id: 'link_4', href: '/meets', title: 'Встречи', icon: meets},
	{id: 'link_5', href: '/clients', title: 'Клиенты', icon: clients},
	{id: 'link_6', href: '/statistic', title: 'Статистика', icon: stats},
];

const MainSidebar = () => {
	return (
		<div className="h-100 position-fixed mt-5 mainSidebar">
			<div className="text-center text-white mainSidebar__sidebar">
				{
					links.map(link => {
						return (
							<Link key={link.id} to={link.href} className='mainSidebar__link'>
								<div className="mainSidebar__point mb-2">
									<img className="pt-1 point-ico" src={link.icon} alt={link.id + 'img'}/>
									<p className="sf-bold pt-2 mb-0 pb-2 text-white mainSidebar__title">
										{link.title}
									</p>
								</div>
							</Link>
						)
					})
				}
			</div>
		</div>
	);
};

export default MainSidebar;