import {ADD_NEW_TASK, UPLOAD_NEW_TASK, TASK_FORM, UPLOAD_URL} from "../../constants";
import {taskFetchUpload} from "../actions/addTask";



export function addTask(state = TASK_FORM, {type, key, value}) {
	switch (type) {
		case ADD_NEW_TASK:
			return {...state, [key]: value};
		case UPLOAD_NEW_TASK:
			const data = JSON.stringify(state);
			console.log(data);
			taskFetchUpload(UPLOAD_URL, data, 'POST');
			return TASK_FORM;
		default:
			return state;
	}
}