import React, {Component} from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';

import './ScheduleActionBtn.css';

class ScheduleActionBtn extends Component{
	state = {
		active: this.props.active
	};

	render() {
		const {className, text, onClick} = this.props;
		const classes = classNames(
			'btn',
			'btn-purple',
			'waves-effect',
			'waves-light',
			className,
			{ 'scheduleActionBtn--active': this.state.active}
		);
		return (
			<button className={classes}
			        onClick={onClick}
			>
				{text}
			</button>
		);
	}
}

ScheduleActionBtn.propTypes = {
	text: PropTypes.string,
	className: PropTypes.string,
	onClick: PropTypes.func,
};

ScheduleActionBtn.defaultProps = {
	text: '',
	className: '',
	onClick: () => {},
};

export default ScheduleActionBtn;