import React, {Component} from 'react';
import CategoryButton from "../categoryButton/CategoryButton";
import AddBlock from "../addBlock/AddBlock";
import Modal from "../Modal/Modal";
import AddNewCalls from "../addNewCalls/AddNewCalls";
import ToDoBlock from "../toDoBlock/ToDoBlock";


class ToDoList extends Component {
	state = {
		isOpen: false
	};
	showModal = () => {
		this.setState({isOpen: !this.state.isOpen})
	};
	closeModal = () => {
		this.setState({isOpen: false})
	};
	render() {
		return (
			<div className="px-0 h-auto col-lg-4 col-15">
				<CategoryButton title='Список дел'
				                color='indigo'
				/>
				<AddBlock title='Записи' type='calls' onClick={this.showModal}/>
				<ToDoBlock/>
				{this.state.isOpen &&
				<Modal onSubmitText='Добавить' title='Добавить запись' isOpen={this.state.isOpen} onCancel={this.closeModal} theme='calls'>
					<AddNewCalls/>
				</Modal>
				}
			</div>
		);
	}
}

export default ToDoList;