import {ADD_NEW_TASK, UPLOAD_NEW_TASK} from "../../constants";

export function addNewTask(key, value) {
	return {
		type: ADD_NEW_TASK,
		key,
		value
	}
}
export function uploadNewTask() {
	return {
		type: UPLOAD_NEW_TASK,
	}
}
export function taskFetchUpload(url, data, method) {
	fetch(url, {
		method: method,
		headers: {
			'Content-Type': 'application/json',
		},
		body: data
	})
		.then((response) => {
			if (!response.ok) {
				throw Error(response.statusText);
			}
			return console.log(response);
		})
		.catch(() => console.log('Have some problems with creating task'));
}