import {TASKS_FETCH_DATA_SUCCESS, TASKS_HAS_ERRORED, TASKS_IS_LOADING} from "../../constants";

const TASKS = {
	tasks: []
};

export function tasksHasErrored(state = false, {type, hasErrored}) {
	switch (type) {
		case TASKS_HAS_ERRORED:
			return hasErrored;

		default:
			return state;
	}
}

export function tasksIsLoading(state = false, {type, isLoading}) {
	switch (type) {
		case TASKS_IS_LOADING:
			return isLoading;

		default:
			return state;
	}
}

export function tasks(state = TASKS, {type, tasks}) {
	switch (type) {
		case TASKS_FETCH_DATA_SUCCESS:
			return tasks;
		default:
			return state;
	}
}