import React, {Component, Fragment} from 'react';
import MainSidebar from "./components/mainSidebar/mainSidebar";
import {BrowserRouter as Router, Switch, Route, Redirect} from "react-router-dom";
import MainHeader from "./components/mainHeader/mainHeader";
import Home from "./pages/Home/Home";
import Reports from "./pages/Reports/Reports";
import LogIn from "./pages/LogIn/LogIn";
import {connect} from "react-redux";
import {checkLoginStatus, logIn} from "./store/actions/logIn";

class App extends Component{
    componentDidMount() {
        this.props.logIn(this.props.login);
        this.props.checkLoginStatus();
    }
    render() {
      const {loggedIn} = this.props;
        return (
            <Router>
                <div className="App container-fluid">
                    <div className="row">
                        {loggedIn &&
                        <Fragment>
                            <MainHeader user={this.props.user}/>
                            <MainSidebar/>
                        </Fragment>
                        }
                        <Switch>
                            {loggedIn
                                ?
                                <Fragment>
                                    <Redirect to='/home'/>
                                    <Route path='/home' render={() => <Home/>}/>
                                    <Route path='/report' render={() => <Reports/>}/>
                                </Fragment>
                                :
                                <Fragment>
                                    <Redirect to='/login'/>
                                    <Route path='/login' render={() => <LogIn/>}/>
                                </Fragment>
                            }
                        </Switch>
                    </div>
                </div>
            </Router>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        loggedIn: state.initialSettings.logged,
        login: state.initialSettings.login,
        user: state.initialSettings.user.user,
    };
};
const mapDispatchToProps = (dispatch) => {
    return {
        checkLoginStatus: () => dispatch(checkLoginStatus()),
        logIn: (data) => dispatch(logIn(data))
    }
};
export default connect(mapStateToProps, mapDispatchToProps)(App);
