import React, {Component} from 'react';
import Icon from "../Icon/Icon";
import Modal from "../Modal/Modal";
import TaskTimer from "./TaskTimer";
import TaskInfo from "../taskInfo/TaskInfo";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import {FETCH_URL, FETCH_URL_ADMIN} from "../../constants";

class TaskItem extends Component {
	state = {
		time: 0,
		actions: false,
		status: false,
		isDone: false,
		isTask: false,
		switchEmployee: false,
		timerStart: false
	};
	timer = this.props.task.timer;
	counter = this.props.task.timer;
	showActions = (type) => {
		this.setState({actions: type})
	};
	handleTask = (action) => {
		this.setState({
			status: action,
			isTask: false,
			timerStart: true
		});
		if (!action) {
			clearInterval(this.timer);
			this.props.pauseTask(this.props.task, this.state.time)
		}
		else {
			this.timer = setInterval(() => {
				this.setState({
					time: this.counter += 1
				})
			}, 1000);
		}
	};
	switchEmployee = (type) => {
		this.props.openTask(this.props.task);
		this.setState({switchEmployee: type})
	};
	saveEmployee = () => {
		this.setState({switchEmployee: false});
		this.props.saveChange(this.props.task, 'PUT')
	};
	openTask = () => {
		this.setState({
			isTask: !this.state.isTask,
			actions: false
		});
		this.props.openTask(this.props.task)
	};
	doneTask = () => {
		this.setState({
			isDone: !this.state.isDone,
			actions: false
		});
	};
	saveChanges = () => {
		this.props.saveChange(this.props.editingTask, "PUT");
		this.setState({isTask: false});
		const url = this.props.user === 2 ? FETCH_URL_ADMIN : FETCH_URL;
		this.props.fetchData(url, this.props.token);
	};
	render() {
		let color;
		const {name, priority} = this.props.task;
		if (priority === '1') color = '#E76161';
		else if (priority === '2') color = '#F9C02E';
		else color = '#3F2FD2';
		return (
			<div className="taskItem"
			     onMouseEnter={() => this.showActions(true)}
			     onMouseLeave={() => this.showActions(false)}
			     style={{ borderLeft: `5px solid ${color}`}}

			>
				<div className="taskItem__name"
				     onClick={this.openTask}
				>
					<span>{name}</span>
				</div>
				<span className='taskItem__divider'/>
				<div className="taskItem__time">
					<TaskTimer isOpen={this.state.timerStart} time={this.state.time}/>
				</div>
				{this.state.actions &&
				<div className="taskItem__actions">
					{this.props.user === 2 &&
						<span onClick={() => this.switchEmployee(true)} className='taskItem__action taskItem__switch'><Icon name='user'/></span>
					}
					{!this.state.status
						? <span onClick={() => this.handleTask(true)} className='taskItem__action taskItem__start'><Icon name='play'/></span>
						: <span onClick={() => this.handleTask(false)} className='taskItem__action taskItem__pause'><Icon name='pause'/></span>
					}
					<span onClick={this.doneTask} className='taskItem__action taskItem__mark'><Icon name='check'/></span>
				</div>
				}
				{this.state.isTask &&
				<Modal onSubmit={this.props.user === 2 ? this.saveChanges : this.openTask}
				       onSubmitText={this.props.user === 2 ? 'Сохранить изменения' : 'Закрыть'}
				       onCancel={this.openTask}
				       isOpen={this.state.isTask}
				       title={this.props.user === 2 ? 'Изменить задачу' : 'Подробности задачи'}
				>
					{this.props.user === 2
						? <TaskInfo userList={this.props.userList}
						            saveChange={this.props.saveChange}
						            editTask={this.props.editTask}
						            task={this.props.editingTask}/>
						: ''
					}
				</Modal>
				}
				{this.state.isDone &&
				<Modal onSubmitText='Завершить'
				       onSubmit={this.doneTask}
				       onCancel={this.doneTask}
				       isOpen={this.state.isDone}
				       title='Комментарий к задаче'
				>
					Закончить задачу
				</Modal>
				}
				{this.state.switchEmployee &&
					<Modal isOpen={this.state.switchEmployee}
					       onCancel={() => this.switchEmployee(false)}
					       onSubmit={this.saveEmployee}
					       title='Выберите исполнителя'
					       onSubmitText='Выбрать'
					>
						<FormControl className='addNewClient__textField'>
							<InputLabel htmlFor="person">Сотрудник</InputLabel>
							<Select
								value={this.props.editingTask.user_id}
								onChange={(e) => this.props.editTask('user_id', e.target.value)}
								inputProps={{
									name: 'person',
									id: 'person',
								}}
							>
								{this.props.userList && this.props.userList.map(user => {
									return (
										<MenuItem value={user.id}>
											<img src={user.avatar} alt=""/>
											{user.name}
										</MenuItem>
									)
								})}
							</Select>
						</FormControl>
					</Modal>
				}
			</div>
		);
	}
}

export default TaskItem;