import React from 'react';
import CategoryButton from "../categoryButton/CategoryButton";
import {connect} from "react-redux";
import TasksListItem from "./TasksListItem";

import './AllTasksList.css'
import Icon from "../Icon/Icon";
import {editTask, openTask, saveChangedTask} from "../../store/actions/editTask";

const AllTasksList = ({tasks, users, user, editingTask, openTask, userList, saveChangedTask, editTask}) => {
	return (
		<div className="px-0 h-auto col-lg-3 col-15">
			<CategoryButton title='Список всех задач'
			                color='indigo'
			/>
			{users.map(person => {
				return (
					<div key={`taskList_${person.id}`} className='col-15 allTasksList px-3'>
						<div className='allTasksList__user'>
							<span className='allTasksList__icon'><Icon name='user'/></span>
							{person.name}
						</div>
						{tasks.map(task => {
							if (task.user_id === person.id) {
								return (
									<TasksListItem editingTask={editingTask}
									               user={user}
									               key={`taskItem_${task.id}`}
									               task={task}
									               openTask={openTask}
									               userList={userList}
									               saveChangedTask={saveChangedTask}
									               editTask={editTask}

									/>
								)
							} else return null;
						})}

					</div>
				)
			})}

		</div>
	);
}

const mapStateToProps = (state) => {
	return {
		tasks: state.tasks.tasks,
		users: state.users,
		user: state.initialSettings.user.user.role_id,
		userList: state.users,
		editingTask: state.editTask,
	};
};
const mapDispatchToProps = (dispatch) => {
	return {
		openTask: (task) => dispatch(openTask(task)),
		saveChangedTask: (obj, method) => dispatch(saveChangedTask(obj, method)),
		editTask: (key, value) => dispatch(editTask(key, value)),
	}
};
export default connect(mapStateToProps, mapDispatchToProps)(AllTasksList);