import {EDIT_TASK, OPEN_TASK, TASK_FORM, UPLOAD_CHANGED_TASK} from "../../constants";
export function editTask(state = {}, {type, task, key, value}) {
	switch (type) {
		case OPEN_TASK:
			return task;
		case EDIT_TASK:
			return {...state, [key]: value};
		case UPLOAD_CHANGED_TASK:
			return TASK_FORM;
		default:
			return state
	}
}