import React from 'react';
import Icon from "../Icon/Icon";
import classNames from 'classnames';

import './AddBlock.css';


const AddBlock = ({title, onClick, className, type}) => {
	const classes = classNames(
		'mt-3 mx-lg-3 mx-0 d-flex align-items-center py-2 px-3 addBlock',
		`addBlock--${type}`,
		className
	);
	return (
		<div className={classes}>
			<p className="text-dark sf-bold mb-0 addBlock__title">
				{title}
			</p>
			<div className="ml-auto" onClick={onClick}>
				<Icon name="plus" className='ico-delete'/>
			</div>
		</div>
	);
};

AddBlock.defaultProps = {
	type: 'tasks',
};

export default AddBlock;