import React, {Component} from 'react';
import TextField from '@material-ui/core/TextField';
import DateFnsUtils from '@date-io/date-fns';
import 'date-fns';
import {MuiPickersUtilsProvider, KeyboardDatePicker, KeyboardTimePicker} from '@material-ui/pickers';

import './AddNewTask.css';
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";

const times = [];
const secondsToHms = (time) => {
	time = Number(time);
	let h = Math.floor(time / 3600);
	let m = Math.floor(time % 3600 / 60);
	let s = Math.floor(time % 60);
	let hDisplay = h > 0 ? (h < 10 ? '0' + h : h) + ' : ' : "00 : ";
	let mDisplay = m > 0 ? (m < 10 ? '0' + m : m) + '' : "00";
	let sDisplay = s > 0 ? (s < 10 ? '0' + s : s) : " 00 ";
	return hDisplay + mDisplay;
};
for (let i = 0; i < 22300; i+=900) {
	times.push(secondsToHms(i));
}

class AddNewTask extends Component {
	setSelectedDate = (date) => {
		const new_date = date.toISOString().substr(0, 10) + ' ' + date.toISOString().substr(11, 8);
		this.props.setDateTime('end_date', new_date)
	};
	setSelectedTime = (time) => {
		const new_time = time.toISOString().substr(0, 10) + ' ' + time.toISOString().substr(11, 8);
		console.log(new_time);
		this.props.setDateTime('time', new_time)
	};
	render() {
		const {name, user_id, time, priority, end_date, desc} = this.props.newTask;
		const {handleChange} = this.props;
		return (
			<div className='addNewTask'>
				<TextField
					label="Название"
					value={name}
					onChange={this.props.handleChange('name')}
					className='addNewTask__textField'
					margin="dense"
				/>
				<FormControl className='addNewClient__textField'>
					<InputLabel htmlFor="person">Сотрудник</InputLabel>
					<Select
						value={user_id}
						onChange={handleChange('user_id')}
						inputProps={{
							name: 'person',
							id: 'person',
						}}
					>
						{this.props.userList.map(user => {
							return (
								<MenuItem key={`user_${user.id}`} value={user.id}>
									<img src={user.avatar} alt=""/>
									{user.name}
								</MenuItem>
							)
						})}
					</Select>
				</FormControl>
				<FormControl className='addNewClient__textField'>
					<InputLabel htmlFor="time">Время выполнения</InputLabel>
					<Select
						value={time}
						onChange={handleChange('time')}
						inputProps={{
							name: 'time',
							id: 'time',
						}}
					>
						{times.map(time => {
							return (
								<MenuItem value={time}>{time}</MenuItem>
							)
						})}
					</Select>
				</FormControl>
				<FormControl className='addNewClient__textField'>
					<InputLabel htmlFor="priority">Приоритет</InputLabel>
					<Select
						value={priority}
						onChange={handleChange('priority')}
						inputProps={{
							name: 'priority',
							id: 'priority',
						}}
					>
						<MenuItem value='1'>Срочный</MenuItem>
						<MenuItem value='2'>Обычный</MenuItem>
						<MenuItem value='3'>Не срочный</MenuItem>
					</Select>
				</FormControl>
				<TextField
					label="Описание"
					multiline
					value={desc}
					onChange={handleChange('desc')}
					rows={4}
					className='addNewTask__textField'
					margin="normal"
				/>
				<MuiPickersUtilsProvider utils={DateFnsUtils}>
					<KeyboardDatePicker
						margin="normal"
						className='addNewTask__textField mt-4'
						label="Срок сдачи"
						value={end_date}
						onChange={this.setSelectedDate}
						format="yyyy-MM-dd"
						KeyboardButtonProps={{
							'aria-label': 'change date',
						}}
					/>
				</MuiPickersUtilsProvider>
			</div>
		);
	}
}

export default AddNewTask;