import React, {Component} from 'react';
import ToDoItem from "./ToDoItem";
import './ToDoBlock.css'

const files = [
	{id: 1, name: 'File'},
	{id: 2, name: 'Lolo'},
	{id: 3, name: 'Hoho'}
]

class ToDoBlock extends Component {
	render() {
		return (
			<div className='col-14 toDoBlock mx-auto mt-3 py-3'>
				{files.map((file, index) => {
					return (
						<ToDoItem key={`file_${index}`} name={file.name}/>
					)
				})}
			</div>
		);
	}
}

export default ToDoBlock;