import React, {Component} from 'react';
import './TaskInfo.css'
import Icon from "../Icon/Icon";
import TextField from "@material-ui/core/TextField";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import {KeyboardDatePicker, KeyboardTimePicker, MuiPickersUtilsProvider} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";


const times = [];
const secondsToHms = (time) => {
	time = Number(time);
	let h = Math.floor(time / 3600);
	let m = Math.floor(time % 3600 / 60);
	let s = Math.floor(time % 60);
	let hDisplay = h > 0 ? (h < 10 ? '0' + h : h) + ' : ' : "00 : ";
	let mDisplay = m > 0 ? (m < 10 ? '0' + m : m) + '' : "00";
	let sDisplay = s > 0 ? (s < 10 ? '0' + s : s) : " 00 ";
	return hDisplay + mDisplay;
};
for (let i = 0; i < 22300; i+=900) {
	times.push(secondsToHms(i));
}
class TaskInfo extends Component {
	editTask = (field) => e => {
		e.stopPropagation();
		this.props.editTask(field, e.target.value)
	};
	saveChange = () => {
		this.props.saveChange();
	};
	deleteTask = () => {
		alert('Deleted')
	};
	render() {
		const {name, desc, user_id, time, end_date, priority} = this.props.task;

		return (
			<div className='taskInfo'>
				<span className='taskInfo__edit'
				      onClick={() => { if (window.confirm('Удалить задачу?')) this.deleteTask() }}
				>
					<Icon name='trash'/>
				</span>
				<TextField
					label="Название"
					value={name}
					onChange={this.editTask('name')}
					className='addNewTask__textField'
					margin="dense"
				/>
				<FormControl className='addNewClient__textField'>
					<InputLabel htmlFor="person">Сотрудник</InputLabel>
					<Select
						onChange={this.editTask('user_id')}
						value={user_id}
						inputProps={{
							name: 'person',
							id: 'person',
						}}
					>
						{this.props.userList && this.props.userList.map(user => {
							return (
								<MenuItem key={`user_${user.id}`} value={user.id}>
									<img src={user.avatar} alt=""/>
									{user.name}
								</MenuItem>
							)
						})}

					</Select>
				</FormControl>
				<FormControl className='addNewClient__textField'>
					<InputLabel htmlFor="time">Время выполнения</InputLabel>
					<Select
						value={time}
						onChange={this.editTask('time')}
						inputProps={{
							name: 'time',
							id: 'time',
						}}
					>
						{times.map(time => {
							return (
								<MenuItem value={time}>{time}</MenuItem>
							)
						})}
					</Select>
				</FormControl>
				<FormControl className='addNewClient__textField'>
					<InputLabel htmlFor="priority">Приоритет</InputLabel>
					<Select
						value={priority}
						onChange={this.editTask('priority')}
						inputProps={{
							name: 'priority',
							id: 'priority',
						}}
					>
						<MenuItem value='1'>Срочный</MenuItem>
						<MenuItem value='2'>Обычный</MenuItem>
						<MenuItem value='3'>Не срочный</MenuItem>
					</Select>
				</FormControl>
				<TextField
					label="Описание"
					multiline
					onChange={this.editTask('desc')}
					value={desc}
					rows={4}
					className='addNewTask__textField'
					margin="normal"
				/>
				<MuiPickersUtilsProvider utils={DateFnsUtils}>
					<KeyboardDatePicker
						margin="normal"
						className='addNewTask__textField mt-4'
						label="Срок сдачи"
						value={end_date}
						format="yyyy-MM-dd"
						KeyboardButtonProps={{
							'aria-label': 'change date',
						}}
					/>
				</MuiPickersUtilsProvider>
			</div>
		);
	}
}

export default TaskInfo;