import React from 'react';
import PropTypes from 'prop-types';
import Icon from "../Icon/Icon";
import Portal from "../Portal";
import classNames from 'classnames';

import './Modal.css';
import Button from "../Button/Button";


const Modal = ({
	               title, isOpen, onCancel, onSubmit, children, theme, cancelButton, onSubmitText
               }) => {

	const headerTheme = classNames(
		'modalHeader',
		`modalHeader--${theme}`
	);
	return (
		<>
			{ isOpen &&
			<Portal>
				<div className="modalOverlay">
					<div className="modalWindow">
						<div className={headerTheme}>
							<div className="modalTitle">{title}</div>
							<Icon name="times" onClick={onCancel} />
						</div>
						<div className="modalBody">
							{children}
						</div>
						<div className="modalFooter">
							{cancelButton && <Button onClick={onCancel} invert>Cancel</Button>}
							<Button theme={theme} onClick={onSubmit}>{onSubmitText}</Button>
						</div>
					</div>
				</div>
			</Portal>
			}
		</>
	);
};
Modal.propTypes = {
	title: PropTypes.string,
	isOpen: PropTypes.bool,
	onCancel: PropTypes.func,
	onSubmit: PropTypes.func,
	children: PropTypes.node,
};
Modal.defaultProps = {
	title: 'Modal title',
	isOpen: false,
	onCancel: () => {},
	onSubmit: () => {},
	children: null,
};
export default Modal;