import React, {Component} from 'react';
import {Link} from "react-router-dom";
import Modal from "../Modal/Modal";
import AddClient from "../addClient/AddClient";

import './mainHeader.css'



class MainHeader extends Component {
	state = {
		isOpen: false,
		isOpenModal: false
	};
	logOut = () => {
		alert('Log Out');
		this.setState({isOpen: false})
	};
	showDrop = () => {
		this.setState({isOpen: !this.state.isOpen})
	};
	showModal = () => {
		this.setState({isOpenModal: !this.state.isOpenModal})
	};
	closeModal = () => {
		this.setState({isOpenModal: false})
	};
	render() {
		const links = [
			{id: 'header_link_1', title: 'КЛИЕНТЫ', href: '/customer'},
			{id: 'header_link_2', title: 'ДОБАВИТЬ КЛИЕНТА', func: this.showModal, href: '#'},
			{id: 'header_link_3', title: 'ОТЧЕТЫ', href: '/report'}
		];
		const actions = [
			{id: 'header_link_4', title: 'ЗАДАЧА +', href: '/task'},
			{id: 'header_link_5', title: 'БРИФ +', href: '/bref'}
		];
		return (
			<nav className="navbar navbar-expand-lg fixed-top mainNav">
				<Link className="text-white logo sf-black pt-0 mainNav__logo"
				      to='/home'>
					.Moore
				</Link>
				<div className="collapse navbar-collapse pl-5" id="navbarNav">
					<ul className="navbar-nav w-100">
						{links.map(link => {
							return (
								<li key={link.id} className="mainNav__link">
									<Link className="nav-link sf-medium text-white waves-effect waves-light"
									      to={link.href}
									      onClick={link.func}
									>
										{link.title}
										<span className="sr-only">(current)</span>
									</Link>
								</li>
							)
						})
						}
						<div className='mainNav__line'/>
						{actions.map(action => {
							return (
								<li key={action.id} className="mainNav__link">
									<Link className="nav-link sf-medium text-white waves-effect waves-light"
									      to={action.href}

									>
										{action.title}
									</Link>
								</li>
							)
						})
						}
						<div className='mainNav__line'/>
						<li className="mainNav__link">
							<a className="nav-link sf-medium text-white waves-effect waves-light"
							      href='http://s.to-moore.com/'
							      target='_blank'
						          rel="noopener noreferrer"
							>
								СКРИПТ
							</a>
						</li>
						<li className="mainNav__link">
							<div className="nav-link sf-medium text-white waves-effect waves-light"
							     onClick={this.logOut}
							>
								ДОБАВИТЬ ЗАДАЧУ
							</div>
						</li>
						<li className="nav-item dropdown ml-auto">
							<div className="nav-link text-white waves-effect waves-light mainNav__logout"
								 onClick={this.showDrop}
							>
								{this.props.user.name ? this.props.user.name : 'Unknown'}
								<span className="caret"/>
							</div>

						</li>
					</ul>
					{this.state.isOpen &&
					<div className="mainNav__drop dropdown-menu-right">
						<div className="dropdown-item text-dark waves-effect waves-light"
						     onClick={this.logOut}
						>
							Выйти
						</div>
					</div>
					}
				</div>
				{this.state.isOpenModal &&
				<Modal onSubmitText='Добавить' title='Добавить нового клиента' isOpen={this.state.isOpenModal} onCancel={this.closeModal} theme='clients'>
					<AddClient/>
				</Modal>
				}
			</nav>
		);
	};
}

export default MainHeader;