import React, {Component} from 'react';
import Modal from "../Modal/Modal";
import TaskInfo from "../taskInfo/TaskInfo";

class TasksListItem extends Component {
	state = {
		isOpen: false
	};
	handleOpen = () => {
		this.props.openTask(this.props.task);
		this.setState({isOpen: true})
	};
	handleClose = (e) => {
		e.stopPropagation();
		this.setState({isOpen: false})
	};
	saveChanges = (e) => {
		e.stopPropagation();
		this.props.saveChangedTask(this.props.editingTask, "PUT");
		this.setState({isOpen: false})
	};

	render() {
		const {task, editingTask, userList, editTask} = this.props;
		const {isOpen} = this.state;
		return (
			<div className='allTasksList__item'
				 onClick={this.handleOpen}
			>
				{task.name}
				<Modal onSubmit={this.saveChanges}
				       onSubmitText='Сохранить изменения'
				       onCancel={this.handleClose}
				       isOpen={isOpen}
				       title='Изменить задачу'
				>
					<TaskInfo saveChange={this.saveChange}
					          editTask={editTask}
					          task={editingTask}
					          userList={userList}
					/>
				</Modal>
			</div>
		);
	}
}

export default TasksListItem;