import React, {Component} from 'react';
import ScheduleActionBtn from "../../components/scheduleActionBtn/ScheduleActionBtn";
import {connect} from "react-redux";
import ReportsDayItem from "../../components/reportsDayItem/ReportsDayItem";

class Reports extends Component {
	render() {
		return (
			<div className='container-fluid pageReports'>
				<div className="pageReports__actions col-15 d-flex py-5">
					<ScheduleActionBtn text='Сегодня' active/>
					<ScheduleActionBtn text='За неделю'/>
					<ScheduleActionBtn text='За месяц'/>
				</div>
				<div className="pageReports__schedule col-15">
					{this.props.reports.map(report => {
						const {date, tasks} = report;
						return (
							<ReportsDayItem day={date} tasks={tasks}/>
						)
					})}
				</div>
			</div>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		reports: state.reports,
	};
};
const mapDispatchToProps = (dispatch) => {
	return {

	}
};

export default connect(mapStateToProps, mapDispatchToProps)(Reports);