import {GET_USERS_URL, SAVE_USERS_LIST} from "../../constants";

export const getUserList = (token) => {
	return (dispatch) => {
		fetch(GET_USERS_URL, {
			method: 'GET',
			headers: {
				'Content-Type': 'application/json',
				'Authorization': `Bearer ${token}`
			},
		})
			.then((response) => {
				if (!response.ok) {
					throw Error(response.statusText);
				}
				console.log('Got User List');
				return response;
			})
			.then((response) => response.json())
			.then((users) => dispatch(saveUsers(users)))

			.catch((response) => console.log(response));
	};
};
export const saveUsers = (list) => {
	return {
		type: SAVE_USERS_LIST,
		list
	}
}