import {EDIT_TASK, FETCH_URL_ADMIN, OPEN_TASK, PAUSE_URL, TASK_PAUSED, UPLOAD_CHANGED_TASK} from "../../constants";
import {taskFetchUpload} from "./addTask";

export const editTask = (key, value) => ({ type: EDIT_TASK, key, value });
export const openTask = (task) => ({ type: OPEN_TASK, task });
export const pauseTask = (task, timer) => {
	return dispatch => {
		fetch(PAUSE_URL, {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
			},
			body: `{"task_id":"${task.id}", "timer":"${timer}"}`
		})
			.then((response) => {
				if (!response.ok) {
					throw Error(response.statusText);
				}
				return console.log(response);
			})
			.then(() => dispatch({type: TASK_PAUSED}))
			.catch(() => console.log('Have some problems with pausing task'));
	}
};
export function saveChangedTask(obj, method) {
	return dispatch => {
		const response = dispatch(
			{
				type: UPLOAD_CHANGED_TASK,
				payload: new Promise((resolve) => {
					const data = JSON.stringify(obj);
					console.log(data);
					resolve(taskFetchUpload(FETCH_URL_ADMIN + `/${obj.id}`, data, method));
				})
			}
		);
		response.then(
			(response) => console.log(response)
		)
	};
}