import React, {Component, Fragment} from 'react';

import './LogIn.css'
import {connect} from "react-redux";
import {editLogin, logIn} from "../../store/actions/logIn";

class LogIn extends Component {
	editLogin = (key) => e => {
		this.props.editLogin(key, e.target.value)
	};
	logIn = () => {
		console.log(JSON.stringify(this.props.login));
		this.props.logIn(this.props.login)
	};
	render() {
		return (
			<Fragment>
				<div className="w-100 mainLogin">
					<div className="mainLogin__container">
						<div className="mainLogin__title">
							MOORE
						</div>
						<div className="mainLogin__form">
							<input onChange={this.editLogin('email')}
							       value={this.props.login.email}
							       className="mainLogin__input"
							       type="text"
							       placeholder='E-mail'
							/>
							<input onChange={this.editLogin('password')}
							       value={this.props.login.password}
							       className="mainLogin__input"
							       type="text"
							       placeholder='Password'
							/>
						</div>
						<div onClick={this.logIn} className="mainLogin__enter">
							Войти
						</div>
					</div>
				</div>
			</Fragment>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		login: state.initialSettings.login,
	};
};
const mapDispatchToProps = (dispatch) => {
	return {
		editLogin: (key, value) => dispatch(editLogin(key, value)),
		logIn: (data) => dispatch(logIn(data))
	}
};

export default connect(mapStateToProps, mapDispatchToProps)(LogIn);