import React, {Component} from 'react';
import TaskItem from "./TaskItem";

import './TaskBlock.css'


class TasksBlock extends Component {
	render() {
		const {tasks, editTask, openTask, saveChange, user, userList, editingTask, token, fetchData, pauseTask} = this.props;
		return (
			<div className='col-14 taskBlock mx-auto mt-3'>
				<span>Активные</span>
				{tasks.map(task => {
					if (task.status) {
						return (
							<TaskItem task={task}
							          user={user}
							          userList={userList}
							          editTask={editTask}
							          editingTask={editingTask}
							          openTask={openTask}
							          token={token}
							          pauseTask={pauseTask}
							          fetchData={fetchData}
							          saveChange={saveChange}
							          key={`task_${task.id}`}
							/>
						)
					} else return null
				})}
				<hr/>
				<span>Все</span>
				{tasks.map(task => {
					if (!task.status) {
						return (
							<TaskItem task={task}
							          editTask={editTask}
							          openTask={openTask}
							          editingTask={editingTask}
							          userList={userList}
							          token={token}
							          pauseTask={pauseTask}
							          fetchData={fetchData}
							          saveChange={saveChange}
							          key={`task_${task.id}`}
							/>
						)
					} else return null
				})}
			</div>
		);
	}
}

export default TasksBlock;