import React, {Component} from 'react';
import CategoryButton from "../categoryButton/CategoryButton";
import AddBlock from "../addBlock/AddBlock";
import Modal from "../Modal/Modal";
import AddNewTask from "../addNewTask/AddNewTask";
import TasksBlock from "../tasksBlock/TasksBlock";
import {connect} from "react-redux";
import {tasksFetchData} from "../../store/actions/tasks";
import {FETCH_URL, FETCH_URL_ADMIN} from "../../constants";
import {addNewTask, uploadNewTask} from "../../store/actions/addTask";
import {editTask, openTask, pauseTask, saveChangedTask} from "../../store/actions/editTask";

class AllTasks extends Component {
	state = {
		isOpen: false,
		tasks: []
	};
	componentDidMount() {
		const url = this.props.user === 2 ? FETCH_URL_ADMIN : FETCH_URL;
		this.props.fetchData(url, this.props.token);
		this.fetchTasks = setInterval(() =>
		this.props.fetchData(url, this.props.token), 2000)
	}
	componentWillUnmount() {
		clearInterval(this.fetchTasks);
	}
	handleChange = (field) => e => {
		this.props.addNewTask([field], e.target.value)
	};
	actionModal = (type) => {
		this.setState({isOpen: type})
	};
	addTask = () => {
		this.props.uploadNewTask();
		this.setState({isOpen: false})
	};
	render() {
		const {tasks, title, hasErrored, userList, user, token, fetchData } = this.props;
		return (
			<div className="px-0 h-auto col-lg-5 col-15 d-lg-block d-none">
				<CategoryButton title='Мои задачи'
				                color='red'
				/>
				{user === 2 && <AddBlock title='Задачи' onClick={() => this.actionModal(true)}/>}
				{hasErrored
					? <div className="col-15 d-flex align-items-center justify-content-center mt-5"><h2>Whoops... We have some errors</h2></div>
					: <TasksBlock openTask={this.props.openTask}
					              editTask={this.props.editTask}
					              editingTask={this.props.editingTask}
					              saveChange={this.props.saveChangedTask}
					              error={this.props.tasksHasErrored}
					              tasks={tasks}
					              pauseTask={this.props.pauseTask}
					              user={user}
					              token={token}
					              fetchData={fetchData}
					              userList={this.props.userList}
					  />
				}
				{this.state.isOpen &&
				<Modal onSubmitText='Добавить'
				       title='Добавить задачу'
				       isOpen={this.state.isOpen}
				       onSubmit={this.addTask}
				       onCancel={() => this.actionModal(false)}
				       theme='tasks'
				>
					<AddNewTask handleChange={this.handleChange}
					            newTask={this.props.taskForm}
					            setDateTime={this.props.addNewTask}
					            title={title}
					            userList={userList}
					/>
				</Modal>
				}
			</div>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		tasks: state.tasks.tasks,
		hasErrored: state.tasksHasErrored,
		isLoading: state.tasksIsLoading,
		taskForm: state.addTask,
		editingTask: state.editTask,
		token: state.initialSettings.token,
		user: state.initialSettings.user.user.role_id,
		userList: state.users,
	};
};
const mapDispatchToProps = (dispatch) => {
	return {
		fetchData: (url, token) => dispatch(tasksFetchData(url, token)),
		addNewTask: (key, value) => dispatch(addNewTask(key, value)),
		uploadNewTask: () => dispatch(uploadNewTask()),
		editTask: (key, value) => dispatch(editTask(key, value)),
		openTask: (task) => dispatch(openTask(task)),
		saveChangedTask: (obj, method) => dispatch(saveChangedTask(obj, method)),
		pauseTask: (task, timer) => dispatch(pauseTask(task, timer))
	}
};
export default connect(mapStateToProps, mapDispatchToProps)(AllTasks);