import {
	CHECKING_LOGIN_STATUS,
	EDIT_LOGIN, GET_USER_URL,
	LOGIN_FAIL,
	LOGIN_SUCCESS,
	LOGIN_URL,
	SAVE_TOKEN,
	SAVE_USER
} from "../../constants";
import {getUserList} from "./users";


export const checkLoginStatus = () => {
	return (dispatch) => {
		const response = dispatch(
			{
				type: CHECKING_LOGIN_STATUS,
				payload: new Promise((resolve) => {
					console.log('Start');
					setTimeout(() => {
						resolve(true)
					}, 5000)
				})
			}
		);
		response.then(
			(response) => console.log(response)
		)
	}
};

export const editLogin = (key, value) => ({
	type: EDIT_LOGIN,
	key,
	value
});
export const loggingSuccess = (token) => {
	getUser(token);
	return {
		type: LOGIN_SUCCESS,
		token,
	}
};
export const loginFail = () => {
	return {
		type: LOGIN_FAIL
	}
};
export const setToken = (token) => {
	return {
		type: SAVE_TOKEN,
		token
	}
};
export const saveUser = (user) => {
	return {
		type: SAVE_USER,
		user
	}
};
export const getUser = (token) => {
	return (dispatch) => {
		fetch(GET_USER_URL, {
			method: 'GET',
			headers: {
				'Content-Type': 'application/json',
				'Authorization': `Bearer ${token}`
			},
		})
			.then((response) => {
				if (!response.ok) {
					throw Error(response.statusText);
				}
				console.log('Got User');
				return response;
			})
			.then((response) => response.json())
			.then((user) => dispatch(saveUser(user)))
			.then(() => dispatch(setToken(token)))
			.then(() => dispatch(loggingSuccess(token)))
			.then(() => dispatch(getUserList(token)))

			.catch((response) => console.log(response));
	};
};
export const logIn = (data) => {
	return (dispatch) => {
		console.log(data);
		fetch(LOGIN_URL, {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
			},
			body: JSON.stringify(data)
		})
			.then((response) => {
				if (!response.ok) {
					throw Error(response.statusText);
				}
				console.log('Login Success');
				return response;
			})
			.then((response) => response.json())
			// .then((token) => dispatch(loggingSuccess(token)))
			.then((token) => dispatch(getUser(token.token)))
			.catch((response) => console.log(response));
	};
};
